*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Forum";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/Forum.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: 'Rouble';
  font-style: normal;
  font-weight: 400;
  src: url('fonts/rouble.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'GT Walsheim Pro';
  font-style: normal;
  font-weight: 400;
  src: url('fonts/GTWalsheimPro-Regular.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'GT Walsheim Pro';
  font-style: normal;
  font-weight: 500;
  src: url('fonts/GTWalsheimPro-Medium.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'GT Walsheim Pro';
  font-style: normal;
  font-weight: 700;
  src: url('fonts/GTWalsheimPro-Bold.woff2') format('woff2');
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  cursor: pointer;
}
.page__body {
  margin: 0;
}
.container {
  width: 375px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 48px;
  }
}
.product-card {
  position: relative;
  padding: 0;
  margin: 0;
}
.product-card__wrapper {
  color: #474442;
  position: relative;
}
.product-card__wrapper::after {
  position: absolute;
  bottom: 0;
  left: 20px;
  right: 20px;
  height: 1px;
  background-color: #e0deda;
  content: '';
}
@media (min-width: 1280px) {
  .product-card__wrapper::after {
    left: 48px;
    right: 48px;
  }
}
@media (min-width: 1280px) {
  .product-card__wrapper {
    display: grid;
    grid-template-columns: 540px auto;
    column-gap: 64px;
    padding-top: 80px;
    grid-template-areas: 'slider header' 'slider detail';
  }
}
.product-card__slider {
  position: relative;
  min-height: 474px;
  grid-area: slider;
}
@media (min-width: 1280px) {
  .product-card__slider-buton {
    top: 228px;
  }
}
.product-card__slider-buton--prev {
  left: 24px;
}
.product-card__slider-buton--next {
  right: 24px;
}
.product-card__chosen-slide {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 375px;
  height: 360px;
  margin-bottom: 24px;
  background-color: #f5f4f2;
  border: none;
}
@media (min-width: 1280px) {
  .product-card__chosen-slide {
    left: 0;
    transform: none;
    height: 500px;
    border: 1px solid #e0deda;
  }
}
.product-card__chosen-image {
  width: auto;
  max-height: 100%;
}
.product-card__back-button {
  position: absolute;
  top: 21px;
  left: 20px;
}
@media (min-width: 1280px) {
  .product-card__back-button {
    display: none;
  }
}
.product-card__slide-list {
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  top: 384px;
  left: 0;
  right: -20px;
  display: flex;
  gap: 8px;
  overflow: hidden;
}
@media (min-width: 1280px) {
  .product-card__slide-list {
    position: relative;
    top: 0;
    gap: 22px;
    width: 100%;
    max-height: 614px;
    padding: 0;
  }
}
.product-card__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 90px;
  height: 90px;
  background-color: #f5f4f2;
  border: 1px solid #e0deda;
  cursor: pointer;
}
.product-card__slide--current {
  border-color: #474442;
}
.product-card__slide-image {
  width: auto;
}
.product-card__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 0;
  gap: 16px;
  grid-area: header;
}
@media (min-width: 1280px) {
  .product-card__header {
    padding: 0;
    margin-bottom: 39px;
    max-width: 380px;
    align-items: start;
  }
}
.product-card__header-title {
  font-family: 'Forum', 'Arial', sans-serif;
  font-size: 32px;
  line-height: 38px;
  font-weight: 400;
  margin: 0;
  letter-spacing: -0.02em;
  text-align: center;
}
@media (min-width: 1280px) {
  .product-card__header-title {
    font-size: 48px;
    line-height: 48px;
    font-weight: 400;
  }
}
.product-card__header-description {
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  margin: 0;
  text-align: center;
}
@media (min-width: 1280px) {
  .product-card__header-description {
    text-align: start;
  }
}
.product-card__header-price {
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  margin-bottom: 8px;
}
@media (min-width: 1280px) {
  .product-card__header-price {
    font-size: 28px;
    line-height: 34px;
    font-weight: 500;
    margin-bottom: 16px;
  }
}
.product-card__header-price-currency {
  color: #acacac;
}
.product-card__header-add-button {
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  letter-spacing: -0.02em;
  width: 100%;
  padding: 19px 0;
  margin: 0;
  border: none;
  color: #ffffff;
  background-color: #474442;
  transition-property: background-color, color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.product-card__header-add-button:hover,
.product-card__header-add-button:focus {
  background-color: #1a1716;
}
.product-card__header-add-button:active {
  color: #acacac;
  background-color: #5f5f5f;
}
.product-card__detail-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 44px;
  grid-area: detail;
}
@media (min-width: 1280px) {
  .product-card__detail-wrapper {
    gap: 18px;
  }
}
.product-card__detail-title {
  font-family: 'Forum', 'Arial', sans-serif;
  font-size: 32px;
  line-height: 38px;
  font-weight: 400;
  margin: 0;
  text-align: center;
  letter-spacing: -0.02em;
  margin-bottom: 4px;
}
@media (min-width: 1280px) {
  .product-card__detail-title {
    font-size: 40px;
    line-height: 40px;
    font-weight: 400;
    text-align: start;
  }
}
.product-card__detail-list-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1px;
}
@media (min-width: 1280px) {
  .product-card__detail-list-wrapper {
    margin-bottom: 5px;
  }
}
.product-card__detail-list-title {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  letter-spacing: -0.02em;
  margin: 0;
  margin-bottom: 7px;
}
.product-card__detail-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.product-card__detail-item {
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  display: grid;
  grid-template-columns: 202px 1fr;
  column-gap: 2px;
  align-items: center;
  padding: 7px 0;
  margin-bottom: 3px;
}
.product-card__detail-name {
  color: #acacac;
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 2px;
}
.product-card__detail-name::after {
  position: relative;
  top: 16px;
  right: 0;
  height: 1px;
  background-color: #e0deda;
  content: '';
}
.product-card__detail-description {
  margin: 0;
}
.swiper-button-prev,
.swiper-button-next {
  display: none;
}
@media (min-width: 1280px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: block;
    position: absolute;
  }
}
.product-tabs {
  position: relative;
  color: #474442;
}
.product-tabs::after {
  position: absolute;
  height: 1px;
  bottom: 104px;
  width: 100%;
  content: '';
  background-color: #e0deda;
}
@media (min-width: 1280px) {
  .product-tabs::after {
    bottom: 128px;
    left: 48px;
    right: 48px;
    width: auto;
  }
}
.product-tabs__wrapper {
  padding: 51px 13px 24px 20px;
}
@media (min-width: 1280px) {
  .product-tabs__wrapper {
    padding: 48px;
  }
}
.product-tabs__tab-button-wrapper {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 28px;
}
@media (min-width: 1280px) {
  .product-tabs__tab-button-wrapper {
    margin-bottom: 61px;
  }
}
.product-tabs__tab-button {
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  font-family: 'Forum', 'Arial', sans-serif;
  letter-spacing: -0.02em;
  padding: 0;
  color: #474442;
  background-color: #ffffff;
  border: none;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.product-tabs__tab-button--not-active {
  opacity: 0.4;
}
.product-tabs__tab-button--not-active::after {
  position: relative;
  bottom: 3px;
  display: block;
  border-bottom: 1px dashed;
  content: '';
  width: 100%;
}
.product-tabs__tab-button:hover {
  opacity: 0.5;
}
@media (min-width: 1280px) {
  .product-tabs__tab-button {
    font-size: 48px;
    line-height: 48px;
    font-weight: 400;
  }
}
.product-tabs__slider {
  display: none;
  position: relative;
  min-height: 288px;
}
.product-tabs__slider--active {
  display: block;
}
@media (min-width: 1280px) {
  .product-tabs__slider-buton {
    top: 135px;
  }
}
.product-tabs__slider-buton--prev {
  left: -22px;
}
.product-tabs__slider-buton--next {
  right: -22px;
}
.product-tabs__list {
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  left: 0;
  right: -11px;
  display: flex;
  gap: 8px;
  overflow: hidden;
  margin-bottom: 28px;
}
@media (min-width: 1280px) {
  .product-tabs__list {
    position: relative;
    gap: 24px;
    margin-bottom: 60px;
  }
}
.product-tabs__item {
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
}
.product-tabs__link {
  display: flex;
  flex-direction: column;
  color: #474442;
  text-decoration: none;
}
@media (min-width: 1280px) {
  .product-tabs__link {
    min-width: 278px;
  }
}
.product-tabs__item-image {
  min-width: 164px;
  min-height: 186px;
  max-height: 100%;
  margin-bottom: 16px;
  background-color: #f5f4f2;
}
@media (min-width: 1280px) {
  .product-tabs__item-image {
    min-width: 278px;
    min-height: 320px;
  }
}
.product-tabs__item-title {
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  margin: 0 0 4px;
}
@media (min-width: 1280px) {
  .product-tabs__item-title {
    font-size: 14px;
  }
}
.product-tabs__item-description {
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin-bottom: 8px;
}
@media (min-width: 1280px) {
  .product-tabs__item-description {
    margin-bottom: 12px;
  }
}
.product-tabs__item-price {
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  opacity: 0.5;
}
.product-tabs__catalog-button-wrapper {
  position: relative;
  width: 200px;
  padding: 0 20px;
  margin: 0 auto;
  background-color: #ffffff;
  z-index: 2;
}
.to-catalog-button {
  position: relative;
  z-index: 2;
  font-family: 'GT Walsheim Pro', 'Arial', sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: -0.02em;
  width: 160px;
  height: 160px;
  padding: 0 20px;
  margin: 0 auto;
  background-color: #ffffff;
  border: 1px solid #e0deda;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4px;
  transition-property: background-color, color, border-color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.to-catalog-button path {
  transition: stroke 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.to-catalog-button:hover {
  color: #ffffff;
  background-color: #474442;
  border-color: #474442;
}
.to-catalog-button:hover path {
  stroke: #ffffff;
}
.arrow-button {
  width: 44px;
  height: 44px;
  background-color: #ffffff;
  background-position: 50%;
  border: 1px solid #e0deda;
  border-radius: 50%;
  z-index: 5;
  transition-property: background-color, color, border-color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95);
  cursor: pointer;
}
.arrow-button:hover {
  background-color: #f5f4f2;
  border-color: #474442;
}
.arrow-button:focus {
  border-color: #474442;
}
.arrow-button:active {
  background-color: #f5f4f2;
  border-color: #e0deda;
}
